.App 
  text-align: center
  height: 100%
  width: 100%

.App-header 
  position: fixed
  background-color: #282c34
  min-height: 100vh
  width: 100%
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  font-size: calc(10px + 2vmin)
  color: white

@keyframes App-logo-spin 
  from 
    transform: rotate(0deg)
  
  to 
    transform: rotate(360deg)
  
@function getShadows($n)
    $shadow: ()
    @for $i from 1 through $n
        $shadow: append($shadow, unquote('#{random(100)}vw') unquote('#{random(100)}vh') #fff, comma)
    @return $shadow

$duration: 3337s
$count: 233
@for $i from 1 through 6
  $duration: floor($duration / 2)
  $count: floor($count / 2)
  .layer#{$i}
      position: fixed
      $size: #{$i}px
      width: $size
      height: $size
      border-radius: 50%
      // background-color: red
      box-shadow: getShadows($count)
      animation: moveup $duration linear infinite
      &::after
          content: ''
          position: fixed
          width: inherit
          height: inherit
          left: 0
          top: 100vh
          box-shadow: inherit
          border-radius: inherit

@keyframes moveup
    to
        transform: translateY(-100vh)
